import {AssertionError, doesNotThrow} from 'assert';
import { assert } from 'console';
import { accessibleTarget } from 'pixi.js';

export enum GameState {
  TITLE,
  PLAY,
  WIN,
  GAMEOVER,
}

export type Direction = 'LEFT' | 'RIGHT' | 'UP' | 'DOWN';

const DirectionValue: {[d in Direction]: number[]} = {
  LEFT: [0, -1],
  RIGHT: [0, 1],
  UP: [-1, 0],
  DOWN: [1, 0],
};

export interface State {
  state: GameState;
  board: GameBoard;
}

export interface GameBoard {
  N: number;
  M: number;
  elements: string[][];
}

function isConsonants(s: string): boolean {
  return 'ㄱ' <= s && s <= 'ㅎ';
}

function isVowel(s: string): boolean {
  return 'ㅏ' <= s && s <= 'ㅣ';
}

function noFinalConsonants(s: string): boolean {
  // ( 자음번호 * 588 + 모음번호 * 28 + 종성번호 ) + '가'(44032)
  return (
    '가' <= s && s <= '힣' && (s.charCodeAt(0) - '가'.charCodeAt(0)) % 28 === 0
  );
}

const stringToInitial = new Map<string, number>([
  ['ㄱ', 0],
  ['ㄲ', 1],
  ['ㄴ', 2],
  ['ㄷ', 3],
  ['ㄸ', 4],
  ['ㄹ', 5],
  ['ㅁ', 6],
  ['ㅂ', 7],
  ['ㅃ', 8],
  ['ㅅ', 9],
  ['ㅆ', 10],
  ['ㅇ', 11],
  ['ㅈ', 12],
  ['ㅉ', 13],
  ['ㅊ', 14],
  ['ㅋ', 15],
  ['ㅌ', 16],
  ['ㅍ', 17],
  ['ㅎ', 18]
]);

const stringToFinal = new Map<string, number>([
  ['ㄱ', 1],
  ['ㄲ', 2],
  ['ㄳ', 3],
  ['ㄴ', 4],
  ['ㄵ', 5],
  ['ㄶ', 6],
  ['ㄷ', 7],
  ['ㄹ', 8],
  ['ㄺ', 9],
  ['ㄻ', 10],
  ['ㄼ', 11],
  ['ㄽ', 12],
  ['ㄾ', 13],
  ['ㄿ', 14],
  ['ㅀ', 15],
  ['ㅁ', 16],
  ['ㅂ', 17],
  ['ㅄ', 18],
  ['ㅅ', 19],
  ['ㅆ', 20],
  ['ㅇ', 21],
  ['ㅈ', 22],
  ['ㅊ', 23],
  ['ㅋ', 24],
  ['ㅌ', 25],
  ['ㅍ', 26],
  ['ㅎ', 27],
])

function merge(s1: string, s2: string): string | boolean {
  function _mergable(s1: string, s2: string): string | boolean {
    if (s2 === " ") {
      return s1;
    }
    if (isConsonants(s1) && isVowel(s2) && stringToInitial.has(s1)) {
      return String.fromCharCode((stringToInitial.get(s1) as number) * 588 + (s2.charCodeAt(0) - 'ㅏ'.charCodeAt(0)) * 28 + '가'.charCodeAt(0));
    }
    if (noFinalConsonants(s1) && isConsonants(s2) && stringToFinal.has(s2)) {
      return String.fromCharCode(s1.charCodeAt(0) + (stringToFinal.get(s2) as number));
    }
    return false;
  }
  const r1 = _mergable(s1, s2);
  return r1 === false ? _mergable(s2, s1) : r1;
}
assert(merge('ㄱ', 'ㄴ') === false);
assert(merge('ㅎ', 'ㅏ') === '하');
assert(merge('ㅎ', ' ') === 'ㅎ');

export class SingleGame {
  board: GameBoard;

  constructor(map: string[][]) {
    this.board = {
      N: map.length,
      M: map[0].length,
      elements: map,
    };
  }

  singleMove(dir: Direction, x: number, y: number) {
    let px = x;
    let py = y;
    let nx = x + DirectionValue[dir][0];
    let ny = y + DirectionValue[dir][1];       
    let movable = false;

    while (0 <= nx && nx < this.board.N && 0 <= ny && ny < this.board.M) {
      if (typeof(merge(this.board.elements[nx][ny], this.board.elements[px][py])) === 'string') {
        movable = true;
        break;
      }
      px = nx;
      py = ny;
      nx += DirectionValue[dir][0];
      ny += DirectionValue[dir][1];       
    }

    if (!movable) return;

    while (nx != x || ny != y) {
      assert(typeof(merge(this.board.elements[nx][ny], this.board.elements[px][py])) === 'string');
      this.board.elements[nx][ny] = merge(this.board.elements[nx][ny], this.board.elements[px][py]) as string;
      this.board.elements[px][py] = " ";
      px -= DirectionValue[dir][0];
      py -= DirectionValue[dir][1];       
      nx -= DirectionValue[dir][0];
      ny -= DirectionValue[dir][1];       
    }
  }

  move(dir: Direction) {
    const newBoard: number[][] = new Array(this.board.N);
    for (let i = 0; i < this.board.N; i++) {
      const newRow: number[] = new Array(this.board.M);
      for (let j = 0; j < this.board.M; j++) {
        newRow[j] = 0;
      }
      newBoard[i] = newRow;
    }

    for (let i = 0; i < this.board.N; i++) {
      for (let j = 0; j < this.board.M; j++) {
        if (this.board.elements[i][j] === 'b') {
          this.singleMove(dir, i, j);
          return;
        }
      }
    }
  }
}
